@import "https://fonts.googleapis.com/css2?family=DM+Sans:opsz,wght@9..40,500&family=Inter:wght@400;500;600&family=Poppins:wght@600&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --light-color: #fff;
  --dark-color: #050505;
  --accent-color: #9bb537;
  --stars-color: #eea10c;
  --stars-uncolored: #d9d9d9;
  --btn-text-color: #f8f8f8;
  --green-color: #7ac769;
  --light-theme-background-color: #f8f8f8;
  --light-theme-text-color: #050505;
  --light-modal-text-color: rgba(5, 5, 5, .8);
  --light-modal-background-color: #fff;
  --light-mobile-bg-color: #9bb537;
  --light-hero-btn-bg-color: #f8f8f8;
  --light-theme-semi-transparent-color: rgba(5, 5, 5, .5);
  --light-theme-slider-semi-transparent-color: rgba(248, 248, 248, .5);
  --light-theme-almost-transparent-color: rgba(5, 5, 5, .3);
  --light-theme-transparent-color: rgba(5, 5, 5, .2);
  --light-modal-underline-color: rgba(5, 5, 5, .1);
  --light-modal-scroll-color: rgba(5, 5, 5, .18);
  --lightn-theme-hero-text-slider: rgba(248, 248, 248, .6);
  --lightn-theme-hero-dish-desc: rgba(248, 248, 248, .2);
  --light-disable-color: rgba(5, 5, 5, .1);
  --cards-background-color-for-stars: rgba(0, 0, 0, .26);
  --animation: 250ms cubic-bezier(.23, .86, .79, .77);
  --ss-content-height: 176px !important;
  --ss-border-radius: 15px !important;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--light-theme-background-color);
  font-family: Inter, sans-serif;
  overflow-x: hidden;
}

.dark {
  --light-theme-background-color: #1e1e1e;
  --stars-uncolored: #3c3b3b;
  --light-theme-text-color: #fff;
  --light-theme-semi-transparent-color: rgba(255, 255, 255, .5);
  --light-theme-almost-transparent-color: rgba(255, 255, 255, .3);
  --light-theme-transparent-color: rgba(255, 255, 255, .2);
  --light-modal-text-color: rgba(255, 255, 255, .8);
  --light-modal-underline-color: rgba(255, 255, 255, .5);
  --light-modal-scroll-color: rgba(255, 255, 255, .18);
  --light-modal-background-color: #050505;
  --light-theme-hero-text-slider: rgba(255, 255, 255, .6);
  --lightn-theme-hero-dish-desc: rgba(255, 255, 255, .2);
  --light-mobile-bg-color: #050505;
  --light-hero-btn-bg-color: #9bb537;
  --light-disable-color: rgba(255, 255, 255, .2);
}

.is-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.no-scroll {
  overflow: hidden;
}

.rad-img {
  border-radius: 15px;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 375px;
    padding: 0 20px;
  }

  .sections-flex {
    padding: 40px 20px 80px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }

  .sections-flex {
    gap: 32px;
    padding: 40px 32px 100px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 100px;
  }

  .sections-flex {
    gap: 63px;
    padding: 50px 100px 100px;
  }
}

.base-btn {
  color: var(--btn-text-color);
  background-color: var(--accent-color);
  border: 1px var(--accent-color) solid;
  transition: color var(--animation);
  border-radius: 15px;
  padding: 14px 28px;
}

.base-btn:hover, .base-btn:focus {
  color: var(--dark-color);
  outline: none;
}

.error-el > p {
  text-align: center;
}

.header-link-home.js-link, .header-link-fav.js-link {
  color: var(--accent-color);
}

.header-container {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 45px;
  display: flex;
}

.header-navigation, .header-list {
  display: none;
}

.header-logo {
  color: var(--accent-color);
  text-align: center;
  letter-spacing: -.64px;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.125;
  display: inline-block;
}

.part-logo {
  color: var(--light-theme-text-color);
}

.header-buttons {
  background-color: rgba(0, 0, 0, 0);
  align-items: center;
  gap: 12px;
  display: flex;
}

.header-basket {
  width: 32px;
  height: 32px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

.header-icon-basket {
  fill: var(--light-theme-text-color);
  transition: fill var(--animation);
}

.header-icon-basket:hover, .header-icon-basket:focus {
  fill: var(--accent-color);
}

.header-switch {
  display: none;
}

.header-burger {
  width: 32px;
  height: 32px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

.header-icon-burger {
  stroke: var(--light-theme-text-color);
  transition: stroke var(--animation);
}

.header-icon-burger:focus, .header-icon-burger:hover {
  stroke: var(--accent-color);
}

.switch {
  display: none;
}

.menu {
  width: 100vw;
  height: 100vh;
  z-index: 49;
  transition: opacity var(--animation), visibility var(--animation), left var(--animation);
  background-color: rgba(0, 0, 0, 0);
  position: fixed;
  top: 0;
  left: 0;
}

.menu-content {
  width: 50vw;
  height: 100vh;
  z-index: 50;
  background-color: var(--light-mobile-bg-color);
  transition: opacity var(--animation), visibility var(--animation), left var(--animation);
  flex-direction: column;
  justify-content: space-between;
  padding: 20px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.menu.is-hidden, .menu.is-hidden .menu-content {
  left: -100%;
}

.menu-close {
  width: 32px;
  height: 32px;
  background: var(--light-mobile-bg-color);
  border: none;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  display: flex;
}

.menu-block {
  margin-left: auto;
  margin-right: auto;
}

.icon-menu {
  fill: var(--light-color);
}

.menu-item:not(:last-child) {
  margin-bottom: 16px;
}

.menu-link-home, .menu-link-fav {
  color: #fff;
  text-align: center;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  display: block;
}

.js-link, .menu-link-fav.js-link {
  color: var(--accent-color);
}

.menu-switch {
  width: 46px;
  height: 21px;
  display: inline-block;
  position: relative;
}

.menu-switch input {
  display: none;
}

.menu-slider {
  cursor: pointer;
  background-color: #cecdcd;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.menu-slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: var(--light-color);
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 4px;
}

input:checked + .menu-slider {
  background-color: var(--accent-color);
}

input:focus + .menu-slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .menu-slider:before {
  transform: translateX(18px);
}

.menu-slider.menu-round {
  border-radius: 20px;
}

.menu-slider.menu-round:before {
  border-radius: 60%;
}

@media screen and (min-width: 768px) {
  .header-list {
    align-items: center;
    gap: 16px;
    display: flex;
  }

  .header-link-home, .header-link-fav {
    color: var(--light-theme-text-color);
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  .header-navigation {
    display: block;
  }

  .header-logo {
    letter-spacing: -.72px;
    font-size: 18px;
    line-height: 1.111;
  }

  .header-burger, .menu {
    display: none;
  }

  .switch {
    width: 46px;
    height: 21px;
    display: inline-block;
    position: relative;
  }

  .switch input {
    display: none;
  }

  .slider {
    cursor: pointer;
    background-color: #cecdcd;
    transition: all .4s;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .slider:before {
    content: "";
    height: 18px;
    width: 18px;
    background-color: var(--light-color);
    transition: all .4s;
    position: absolute;
    bottom: 2px;
    left: 4px;
  }

  input:checked + .slider {
    background-color: var(--accent-color);
  }

  input:focus + .slider {
    box-shadow: 0 0 1px #2196f3;
  }

  input:checked + .slider:before {
    transform: translateX(18px);
  }

  .slider.round {
    border-radius: 20px;
  }

  .slider.round:before {
    border-radius: 60%;
  }
}

@media screen and (min-width: 1280px) {
  .header-container {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.err-img {
  margin: 0 auto;
  transform: scale(1.3);
}

.vrap-hero {
  padding-top: 42px;
  padding-bottom: 40px;
  position: relative;
  overflow: hidden;
}

.swiper-pagination > .swiper-pagination-bullet {
  opacity: 1;
  background-color: var(--light-theme-text-color);
}

.swiper-pagination > .swiper-pagination-bullet-active {
  background-color: var(--accent-color);
}

.hero-name {
  color: var(--light-theme-text-color);
  text-transform: uppercase;
  font-family: Inter;
  font-size: 26px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.2;
}

.tasty-green {
  color: var(--accent-color);
  text-transform: uppercase;
  font-family: Inter;
  font-size: 26px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.2;
}

.hero-text {
  color: var(--light-theme-text-color);
  margin-top: 14px;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.28571;
}

.hero-btn {
  border: 1px solid var(--accent-color);
  color: var(--light-theme-text-color);
  transition: color var(--animation), background-color var(--animation), border var(--animation);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  margin-top: 28px;
  padding: 14px 28px;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  display: inline-flex;
}

.hero-btn:hover, .hero-btn:focus {
  background: var(--accent-color);
  color: var(--btn-text-color);
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 14px 28px;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  display: inline-flex;
}

.hero-slider {
  padding-bottom: 40px;
}

.vrap-hero {
  position: relative;
}

.container.wo-padding {
  padding: 0 0 0 20px;
}

.cook {
  object-fit: cover;
  height: 280px;
  max-width: 80px;
  border-radius: 12px;
}

.pic {
  height: 175px;
  max-width: 200px;
}

.zoom-dish {
  border: 1px solid var(--dark-color);
  height: 280px;
  max-width: 197px;
  object-fit: cover;
  object-position: -6% 6px;
  border-radius: 12px;
  transform: scale(1.2);
}

.zoom-two {
  object-fit: cover;
  object-position: 15% -10px;
  transform: scale(1.3);
}

.first-pic {
  padding-right: 8px;
}

.second-pic {
  width: 200px;
  max-height: 302px;
  background-color: var(--dark-color);
  background: var(--accent-color);
  background: radial-gradient(circle, var(--accent-color) -100%, var(--dark-color) 70%);
  border-radius: 12px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-right: 8px;
  display: flex;
  position: relative;
}

.third-pic {
  width: 200px;
  max-height: 302px;
  background-color: var(--dark-color);
  border-radius: 12px;
  margin-right: 16px;
  display: flex;
  overflow: hidden;
}

.swiper {
  height: 302px;
  width: 512px;
  position: absolute;
  overflow: hidden;
}

.my-swiper-wrap {
  width: 100%;
  margin-top: 48px;
  position: relative;
}

.hero-slide-wrap {
  display: flex;
}

.swiper-wrapper {
  position: relative;
}

.dish-description {
  width: 151px;
  color: var(--lightn-theme-hero-text-slider);
  text-align: center;
  text-transform: uppercase;
  font-family: Inter;
  font-size: 10px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.4;
  display: block;
  position: absolute;
  bottom: 24px;
}

.dish-area {
  color: var(--lightn-theme-hero-dish-desc);
  text-align: center;
  font-family: Inter;
  font-size: 8px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
  display: block;
  position: absolute;
  bottom: 14px;
}

.swiper-pagination-bullet {
  position: relative;
  top: 12px;
  left: -11%;
}

@media screen and (min-width: 768px) {
  .hero-name, .tasty-green {
    font-size: 36px;
  }

  .hero-text {
    width: 408px;
    margin-top: 16px;
    font-size: 16px;
    line-height: 1.5;
  }

  .hero-btn {
    margin-top: 32px;
  }

  .container.wo-padding {
    padding: 0 0 0 32px;
  }

  .pic {
    height: 271px;
    max-width: 302px;
  }

  .my-swiper-wrap {
    margin-top: 64px;
  }

  .swiper-wrapper {
    width: 650px;
    height: 500px;
  }

  .cook {
    object-fit: cover;
    height: 442px;
    max-width: 121px;
    border-radius: 12px;
  }

  .zoom-dish {
    height: 378px;
    max-width: 276px;
  }

  .zoom-two {
    object-fit: cover;
    object-position: 6% -6px;
    transform: scale(1.6);
  }

  .first-pic {
    padding-right: 16px;
  }

  .second-pic {
    width: 351px;
    max-height: 442px;
    background-color: var(--dark-color);
    border: 1px solid var(--dark-color);
    border-radius: 12px;
    margin-right: 16px;
    display: flex;
  }

  .third-pic {
    width: 351px;
    max-height: 442px;
    background-color: var(--dark-color);
    border: 1px solid var(--dark-color);
    border-radius: 12px;
    margin-right: 16px;
    display: flex;
    overflow: hidden;
  }

  .hero-name {
    width: 446px;
  }

  .swiper {
    width: 900px;
    height: 468px;
  }

  .dish-description {
    width: 276px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.4;
    bottom: 54px;
  }

  .dish-area {
    font-size: 12px;
    line-height: 1.16;
    display: block;
    position: absolute;
    bottom: 32px;
  }

  .swiper-pagination-bullet {
    left: -15%;
  }
}

@media screen and (min-width: 1280px) {
  .vrap-hero {
    padding-bottom: 50px;
    display: flex;
  }

  .first-pic {
    padding-right: 16px;
  }

  .second-pic {
    width: 351px;
    max-height: 442px;
    border: 1px solid var(--dark-color);
    border-radius: 12px;
    margin-right: 16px;
    display: flex;
  }

  .third-pic {
    width: 351px;
    max-height: 442px;
    background-color: var(--dark-color);
    border: 1px solid var(--dark-color);
    border-radius: 12px;
    margin-right: 16px;
    display: flex;
  }

  .hero-name-btn-block {
    margin-right: 32px;
    padding-top: 64px;
    padding-left: 0;
  }

  .hero-name {
    width: 446px;
  }

  .swiper {
    height: 468px;
    left: 0;
  }

  .my-swiper-wrap {
    margin-top: 0;
    padding-top: 0;
  }

  .container.wo-padding {
    padding: 0 0 0 100px;
  }

  .swiper-horizontal {
    bottom: 0;
  }
}

.categories-section {
  padding: 0 8px 20px 0;
}

.categories-btn {
  background-color: inherit;
  border: 1px var(--light-theme-transparent-color) solid;
  color: var(--light-theme-semi-transparent-color);
  transition: color var(--animation), background-color var(--animation), border var(--animation);
  margin: 0 0 24px;
  font-size: 17px;
  font-weight: 500;
}

.categories-btn:hover, .categories-btn:focus, .categories-btn-active {
  color: var(--btn-text-color);
  background-color: var(--accent-color);
  border: 1px var(--accent-color) solid;
}

.categories-container {
  width: 100%;
  height: 212px;
  overflow: auto;
}

.category {
  margin-bottom: 18px;
}

.category-btn {
  background-color: inherit;
  color: var(--light-theme-almost-transparent-color);
  transition: color var(--animation);
  border: rgba(0, 0, 0, 0);
  padding: 0 10px 0 0;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
}

.category-btn:hover, .category-btn:focus, .category-btn-active {
  color: var(--accent-color);
}

.categories-err {
  color: var(--light-theme-semi-transparent-color);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
}

.categories-container {
  scrollbar-width: thin;
  scrollbar-color: var(--light-modal-scroll-color) inherit;
}

.categories-container::-webkit-scrollbar {
  width: 8px;
}

.categories-container::-webkit-scrollbar-track {
  background: inherit;
}

.categories-container::-webkit-scrollbar-thumb {
  background-color: var(--light-modal-scroll-color);
  width: 8px;
  height: 116px;
  border: rgba(0, 0, 0, 0);
  border-radius: 12px;
}

@media screen and (min-width: 375px) {
  .categories-container {
    width: 326px;
  }
}

@media screen and (min-width: 768px) {
  .categories-section {
    padding: 0 0 32px;
  }

  .categories-btn {
    margin: 0 0 30px;
  }

  .categories-container {
    width: 176px;
    height: 416px;
  }

  .category {
    margin-bottom: 24px;
  }

  .category-btn {
    font-size: 16px;
